import React, { useState, useEffect, useRef } from "react";
import { useLocation, useNavigate } from "react-router-dom";

import axios from "axios";
import './style.css'

const CargarLibros = () => {
    const [libros, setLibros] = useState([]);
    const [autores, setAutores] = useState([]);
    const [categorias, setCategorias] = useState([]);
    const [subcategorias, setSubcategorias] = useState([]);
    const [titulo, setTitulo] = useState('');
    const [categoria, setCategoria] = useState('');
    const [imagen, setImagen] = useState('');
    const [subcategoria, setSubcategoria] = useState('');
    const [descripcion, setDescripcion] = useState('');
    const [cantidad, setCantidad] = useState('');
    const [libroSeleccionado, setLibroSeleccionado] = useState(null);
    const [autor, setAutor] = useState(null);
    const navigate = useNavigate();
    const location = useLocation();
    const formRef = useRef(null);
    // Obtener el token del localStorage
    const token = localStorage.getItem('token');

    useEffect(() => {
        if (!token) {
          navigate('/login');
        }
      }, [token, navigate]);
    
      useEffect(() => {
        axios.get('https://node-mongo-api4.onrender.com/libros')
          .then(response => {
            setLibros(response.data);
          })
          .catch(error => {
            console.log(error);
          });
      }, []);
    
      useEffect(() => {
        axios.get('https://node-mongo-api4.onrender.com/autores')
          .then(response => {
            setAutores(response.data);
          })
          .catch(error => {
            console.log(error);
          });
      }, []);
    
      useEffect(() => {
        axios.get('https://node-mongo-api4.onrender.com/categories')
          .then(response => {
            setCategorias(response.data);
          })
          .catch(error => {
            console.log(error);
          });
      }, []);
    
      useEffect(() => {
        if (categoria) {
          axios.get(`https://node-mongo-api4.onrender.com/categories/${categoria}/subcategories`)
            .then(response => {
              setSubcategorias(response.data);
            })
            .catch(error => {
              console.log(error);
            });
        } else {
          setSubcategorias([]);
        }
      }, [categoria]);
    
      const handleSubmit = (event) => {
        event.preventDefault();
    
        if (libroSeleccionado) {
          axios.put(`https://node-mongo-api4.onrender.com/libros/${libroSeleccionado._id}`, {
            titulo,
            categoria,
            imagen,
            subcategoria,
            descripcion,
            cantidad,
            autor
          })
            .then(response => {
              axios.get('https://node-mongo-api4.onrender.com/libros')
                .then(response => {
                  setLibros(response.data);
                })
                .catch(error => {
                  console.log(error);
                });
            })
            .catch(error => {
              console.log(error);
            });
        } else {
          axios.post('https://node-mongo-api4.onrender.com/libros', {
            titulo,
            categoria,
            imagen,
            subcategoria,
            descripcion,
            cantidad,
            autor
          })
            .then(response => {
              axios.get('https://node-mongo-api4.onrender.com/libros')
                .then(response => {
                  setLibros(response.data);
                })
                .catch(error => {
                  console.log(error);
                });
            })
            .catch(error => {
              console.log(error);
            });
        }
    
        setTitulo('');
        setCategoria('');
        setImagen('');
        setSubcategoria('');
        setDescripcion('');
        setCantidad('');
        setAutor(null);
        setLibroSeleccionado(null);
    
        formRef.current.reset();
      }
    
      const handleEditarLibro = (id) => {
        axios.get(`https://node-mongo-api4.onrender.com/libros/${id}`)
          .then(response => {
            setLibroSeleccionado(response.data);
            setTitulo(response.data.titulo);
            setCategoria(response.data.categoria);
            setImagen(response.data.imagen);
            setSubcategoria(response.data.subcategoria);
            setDescripcion(response.data.descripcion);
            setCantidad(response.data.cantidad);
            setAutor(response.data.autor);
          })
          .catch(error => {
            console.log(error);
          });
      };
    
      const handleEliminarLibro = (id) => {
        if (window.confirm("¿Estás seguro que deseas eliminar este libro?")) {
          axios.delete(`https://node-mongo-api4.onrender.com/libros/${id}`)
            .then(response => {
              axios.get('https://node-mongo-api4.onrender.com/libros')
                .then(response => {
                  setLibros(response.data);
                })
                .catch(error => {
                  console.log(error);
                });
            })
            .catch(error => {
              console.log(error);
            });
        }
      };
    
      const handleImageChange = (event) => {
        const file = event.target.files[0];
        const reader = new FileReader();
    
        reader.onloadend = () => {
          const base64Image = reader.result;
          setImagen(base64Image);
        };
    
        if (file) {
          reader.readAsDataURL(file);
        }
      };
    
      useEffect(() => {
        const searchParams = new URLSearchParams(location.search);
        const id = searchParams.get("id");
    
        axios.get(`https://node-mongo-api4.onrender.com/libros/${id}`)
          .then(response => {
            setLibroSeleccionado(response.data);
            setTitulo(response.data.titulo);
            setCategoria(response.data.categoria);
            setImagen(response.data.imagen);
            setSubcategoria(response.data.subcategoria);
            setDescripcion(response.data.descripcion);
            setCantidad(response.data.cantidad);
            setAutor(response.data.autor);
          })
          .catch(error => {
            console.log(error);
          });
    
      }, [location.search]);

    return (
        <div>
            <form ref={formRef} onSubmit={handleSubmit} className="form-container">
                <h2 className="titulodecarga">Cargar Libros</h2>
                <div className="subgrupo1">
                    <div className="form-group title">
                        <label htmlFor="titulo" className="form-label">Título:</label>
                        <input type="text" className="form-control" id="titulo" value={titulo} onChange={(e) => setTitulo(e.target.value)} />
                    </div>

                    <div className="form-group1">
                        <label htmlFor="descripcion" className="form-label">Descripción:</label>
                        <textarea className="form-control" id="descripcion" value={descripcion} onChange={(e) => setDescripcion(e.target.value)}></textarea>
                    </div>

                </div>

                <div className="subgrupo2">
                    <div className="form-group2">
                        <label htmlFor="imagen" className="form-label">Imagen:</label>
                        <input type="file" accept=".jpg, .jpeg, .png" className="form-control" id="images" onChange={handleImageChange} />
                    </div>

                    <div className="form-group categoria">
                        <label htmlFor="categoria" className="form-label">Categoría:</label>
                        <select className="form-control" id="categoria" value={categoria} onChange={(e) => setCategoria(e.target.value)} style={{ color: '#000' }}>
                            <option value="">Seleccionar categoría</option>
                            {categorias.map(categoria => (
                                <option key={categoria._id} value={categoria.name} style={{ color: '#000' }} >{categoria.name}</option>
                            ))}
                        </select>
                    </div>

                </div>
                <div className="subgrupo4">
                    <div className="form-group subcategoria">
                        <label htmlFor="subcategoria" className="form-label">Subcategoría:</label>
                        <select className="form-control" id="subcategoria" value={subcategoria} onChange={(e) => setSubcategoria(e.target.value)}>
                            <option value="">Seleccionar subcategoría</option>
                            {subcategorias.map(subcategoria => (
                                <option key={subcategoria._id} value={subcategoria._id}>{subcategoria}</option>
                            ))}
                        </select>
                    </div>

                    <div className="form-groupdecantidad">
                        <label htmlFor="cantidad" className="form-label">Cantidad:</label>
                        <input type="number" className="form-control" id="cantidad" value={cantidad} onChange={(e) => setCantidad(e.target.value)} />
                    </div>
                </div>

                <div className="form-group autores">
                    <label htmlFor="autor" className="form-label">Autor:</label>
                    <select className="form-control" id="autor" value={autor} onChange={(e) => setAutor(e.target.value)}>
                        <option value="">Seleccionar autor</option>
                        {autores.map(autor => (
                            <option key={autor._id} value={autor.nombre}>{autor.nombre}</option>
                        ))}
                    </select>
                </div>

                <button type="submit" className="btn btn-success guardarlibro">{libroSeleccionado ? "Editar" : "Guardar"}</button>
            </form>

            <h2>Libros cargados:</h2>
            <div className="supercuadrolibros" style={{ display: 'flex', flexWrap: 'wrap' }}>
                {libros.map((libro, index) => (
                    <div className="cuadroparalibros" key={libro._id} style={{ margin: '10px', textAlign: 'center' }}>
                        <img className="subcuadrolibros" src={libro.imagen} alt={libro.titulo} style={{ width: '200px', height: '300px', objectFit: 'cover' }} />
                        <h3 className="titulodelibros">Titulo:{libro.titulo}</h3>
                        <p>Categoria:{libro.categoria}</p>
                        <p>SubCateg:{libro.subcategoria}</p>
                        <p className="descripcionlibro">Descripcion:{libro.descripcion}</p>
                        <p>Cantidad:{libro.cantidad}</p>

                        <button className="btn btn-success" onClick={() => handleEditarLibro(libro._id)}>Editar</button>
                        <button className="btn btn-danger" onClick={() => handleEliminarLibro(libro._id)}>Eliminar</button>
                    </div>
                ))}
            </div>
        </div>
    );
};

export default CargarLibros;